import React, { useEffect, useState } from "react";
import {
  getProductsByCount,
  fetchProductsByFilter
} from "./../functions/product";
import { useSelector, useDispatch } from "react-redux";
import ProductCard from "./../components/cards/ProductCard";
import { Menu, Slider, Checkbox} from "antd";
import { EuroOutlined, DownSquareOutlined } from "@ant-design/icons";
import { getCategories } from "./../functions/category";
import Search from './../components/forms/Search'

const { SubMenu} = Menu;

const Shop = () => {
  const [products, setProducts] = useState([]);
  const [loading, setLoading] = useState(false);
  const [price, setPrice] = useState([0, 0]);
  const [ok, setOk] = useState(false);
  const [categories, setCategories] = useState([]);
  const [categoryIds, setCategoryIds] = useState([]);

  let dispatch = useDispatch();
  let { search } = useSelector((state) => ({ ...state }));
  const { text } = search;

  useEffect(() => {
    loadAllProducts();
    //fetch categories
    getCategories().then((res) => setCategories(res.data));
  }, []);

  //load products on page load
  const loadAllProducts = () => {
   getProductsByCount(2000).then((p) => {
       
      setProducts(p.data);
      setLoading(false);
    });
  };

  //load products based on user search input
  useEffect(() => {
    const delayed = setTimeout(() => {
      fetchProducts({ query: text });
      if (!text) {loadAllProducts()}
    }, 300);
    return () => clearTimeout(delayed);
  }, [text]);

  const fetchProducts = (arg) => {
    fetchProductsByFilter(arg).then((res) => {
      setProducts(res.data);
    });
  };

  //load products based on price range
  useEffect(() => {
    console.log("ok to request");
    fetchProducts({ price });
  }, [ok]);

  const handleSlider = (value) => {
    dispatch({
      type: "SEARCH_QUERY",
      payload: { text: "" },
    });
    setCategoryIds([]);
    setPrice(value);
    setTimeout(() => {
      setOk(!ok);
    }, 300);
  };

  //load products based on category
  const showCategories = () =>
    categories.map((c) => (
      <div key={c._id}>
        <Checkbox
          onChange={handleCheck}
          className="pb-2 pl-4 pr-4"
          value={c._id}
          name="category"
          checked={categoryIds.includes(c._id)}
        >
          {c.name}
        </Checkbox>

        <br />
      </div>
    ));
// handle check for categories
  const handleCheck = (e) => {
      dispatch({
          type: 'SEARCH_QUERY',
          payload: {text: ''}
      });
      setPrice([0,0])
    //console.log(e.target.value)
    let inTheState = [...categoryIds];
    let justChecked = e.target.value;
    let foundInTheState = inTheState.indexOf(justChecked); // will return true or -1

    if (foundInTheState === -1) {
      inTheState.push(justChecked);
    } else {
      inTheState.splice(foundInTheState, 1);
    }
      setCategoryIds(inTheState);
      console.log(inTheState);
      fetchProducts({ category: inTheState });
    
  };

  return (
    <div className="container-fluid">
      <div className="row">
        <div className="col-md-2 pt-2">
        <hr/>
          <h5>Search / Filter / Sort</h5>
          <hr />

          <Menu defaultOpenKeys={["1", "2"]} mode="inline">
          <Search className="m-4 pb-10" />
          <hr />
            <SubMenu
              key="1"
              title={
                <span className="h6">
                  <EuroOutlined /> Price
                </span>
              }
            >
              <div>
                <Slider
                  className="ml-4 mr-4"
                  tipFormatter={(value) => `€${value}`}
                  range
                  value={price}
                  onChange={handleSlider}
                  max="1000" //or find max price from db
                />
              </div>
            </SubMenu>
            {/* category */}
            <SubMenu
              key="2"
              title={
                <span className="h6">
                  <DownSquareOutlined /> Categories
                </span>
              }
            >
              <div style={{ marginTop: "-10px" }}>{showCategories()}</div>
            </SubMenu>
          </Menu>
        </div>
        {/* main window */}
        <div className="col-md-9 pt-2">
          {loading ? (
            <h4 className="text-danger">Loading...</h4>
          ) : (
            <h4 className="text-danger">Products</h4>
          )}
          {products.length < 1 && <p>No products found</p>}
          <div className="row pb-5">
            {products.map((p) => (
              <div className="col-md-3 pb-3" key={p._id}>
                <ProductCard product={p} />
              </div>
            ))}
          </div>
        </div>
      </div>
    </div>
  );
};

export default Shop;
